import { Component, Output } from '@angular/core';
import { EventEmitter } from '@angular/core';
import {
  AlertService,
  GainsightService,
  FilesService,
  gettext,
  IFetchWithProgress
} from '@c8y/ngx-components';
import { PRODUCT_EXPERIENCE } from './files-repository-product-experience.constants';
import { Observable, Subscription, combineLatest } from 'rxjs';

@Component({
  selector: 'c8y-files-repository-upload',
  templateUrl: './files-repository-upload.component.html'
})
export class FilesRepositoryUploadComponent {
  /** Emits an event when modal is closed. `uploaded` indicates if any files were uploaded. */
  @Output() onClose: EventEmitter<{ uploaded: boolean }> = new EventEmitter();
  isLoading = false;
  dropFilesHereMessage: string = gettext('Drop files here');
  filesToUpload: File[];
  uploadProgress: IFetchWithProgress[];
  uploadProgress$: Observable<IFetchWithProgress[]>;
  uploadSubscription: Subscription;

  constructor(
    private alertService: AlertService,
    private gainsightService: GainsightService,
    private filesService: FilesService
  ) {}

  async upload(): Promise<void> {
    this.isLoading = true;
    this.uploadProgress$ = combineLatest(
      this.filesToUpload.map(file => this.filesService.uploadFileWithProgress$(file))
    );

    this.uploadSubscription = this.uploadProgress$.subscribe(
      uploadProgress => {
        this.uploadProgress = uploadProgress;
      },
      e => {
        if (e) {
          this.alertService.addServerFailure(e);
          this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.EVENTS.FILE_REPOSITORY_UPLOAD, {
            component: PRODUCT_EXPERIENCE.COMPONENTS.FILE_REPOSITORY_UPLOAD_COMPONENT,
            action: PRODUCT_EXPERIENCE.RESULTS.UPLOAD_ITEM_FAILED
          });
        }
        this.isLoading = false;
      },
      () => {
        this.alertService.success(gettext('File(s) uploaded.'));
        this.onClose.emit({ uploaded: true });
        this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.EVENTS.FILE_REPOSITORY_UPLOAD, {
          component: PRODUCT_EXPERIENCE.COMPONENTS.FILE_REPOSITORY_UPLOAD_COMPONENT,
          action: PRODUCT_EXPERIENCE.RESULTS.UPLOAD_ITEM_SUCCESS
        });
      }
    );
  }

  onFileDroppedEvent(event: File[]): void {
    if (event && event.length > 0) {
      this.filesToUpload = event;
    }
  }
}
