import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';
import { FilesRepositoryGuard } from './files-repository.guard';

@Injectable({ providedIn: 'root' })
export class FilesRepositoryNavigationFactory implements NavigatorNodeFactory {
  private navs: NavigatorNode[] = [];

  constructor(private filesRepositoryGuard: FilesRepositoryGuard) {}

  async get(): Promise<NavigatorNode[]> {
    if (this.filesRepositoryGuard.canActivate()) {
      if (!this.navs.length) {
        this.navs.push(
          new NavigatorNode({
            parent: gettext('Management'),
            label: gettext('Files repository'),
            icon: 'c8y-archive',
            path: 'files',
            routerLinkExact: false,
            priority: 1000
          })
        );
      }
    }
    return this.navs;
  }
}
