import { Component, EventEmitter, ViewChild } from '@angular/core';
import { IManagedObjectBinary, InventoryBinaryService } from '@c8y/client';
import { Subject } from 'rxjs';
import { take, takeUntil } from 'rxjs/operators';
import {
  gettext,
  Pagination,
  DisplayOptions,
  Column,
  Row,
  Status,
  LoadMoreMode,
  ServerSideDataResult,
  DataSourceModifier,
  BuiltInActionType,
  ActionControl,
  BulkActionControl,
  ModalService,
  AlertService,
  FilesService,
  ServerSideDataCallback,
  DataGridComponent
} from '@c8y/ngx-components';
import { BsModalService, ModalOptions } from 'ngx-bootstrap/modal';
import { FilesRepositoryUploadComponent } from './files-repository-upload.component';
import { FilesRepositoryService } from './files-repository.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'c8y-files-repository',
  templateUrl: './files-repository.component.html'
})
export class FilesRepositoryComponent {
  @ViewChild(DataGridComponent, { static: true }) dataGrid: DataGridComponent;
  destroy$: Subject<boolean> = new Subject<boolean>();
  title: string = gettext('Files repository');
  managementTitle: string = gettext('Management');
  loadMoreItemsLabel: string = gettext('Load more files');
  loadingItemsLabel: string = gettext('Loading filesâ€¦');

  isLoading = true;

  displayOptions: DisplayOptions = {
    bordered: false,
    striped: true,
    filter: true,
    gridHeader: true
  };

  columns: Column[] = this.filesRepositoryService.getColumns();
  pagination: Pagination = this.filesRepositoryService.getPagination();
  infiniteScroll: LoadMoreMode = 'auto';
  serverSideDataCallback: ServerSideDataCallback;
  returnedDataSize: number;
  selectable = true;
  actionControls: ActionControl[] = [
    {
      text: gettext('Delete'),
      icon: 'trash',
      type: BuiltInActionType.Delete,
      showIf: selectedItem =>
        !this.filesRepositoryService.hasApplicationStorageFragment(selectedItem),
      callback: selectedItem => this.onDeleteItem(selectedItem)
    },
    {
      text: gettext('Download'),
      icon: 'download',
      type: BuiltInActionType.Export,
      callback: selectedItem => this.onDownloadItem(selectedItem as IManagedObjectBinary)
    }
  ];
  bulkActionControls: BulkActionControl[] = [
    {
      type: BuiltInActionType.Delete,
      callback: selectedItemIds => this.onDeleteItems(selectedItemIds)
    }
  ];

  refresh: EventEmitter<any> = new EventEmitter<any>();

  constructor(
    private filesRepositoryService: FilesRepositoryService,
    private inventoryBinaryService: InventoryBinaryService,
    private modalService: ModalService,
    private alertService: AlertService,
    private bsModalService: BsModalService,
    private fileService: FilesService,
    private translateService: TranslateService
  ) {
    // we're setting up `serverSideDataCallback` to execute a method from this component with bound `this`
    this.serverSideDataCallback = this.onDataSourceModifier.bind(this);
  }

  async onDataSourceModifier(
    dataSourceModifier: DataSourceModifier
  ): Promise<ServerSideDataResult> {
    const { res, data, paging } = await this.filesRepositoryService.getData(
      dataSourceModifier.columns,
      dataSourceModifier.pagination,
      dataSourceModifier.searchText
    );

    const filteredSize: number = await this.filesRepositoryService.getCount(
      dataSourceModifier.columns,
      dataSourceModifier.pagination
    );
    const size: number = await this.filesRepositoryService.getTotal();
    const serverSideDataResult: ServerSideDataResult = { res, data, paging, filteredSize, size };
    this.isLoading = false;
    this.returnedDataSize = serverSideDataResult.size;

    return serverSideDataResult;
  }

  async onDeleteItem(selectedItem: Row) {
    return this.deleteItemsWithConfirmation([selectedItem.id], {
      title: gettext('Delete file'),
      body: this.translateService.instant(
        gettext('You are about to delete file "{{ name }}". Do you want to proceed?'),
        selectedItem
      ),
      successText: gettext('File deleted.')
    });
  }

  async onDeleteItems(selectedItemsIds: string[]) {
    const dataGridDataSourceData = await this.dataGrid.dataSource.data$.pipe(take(1)).toPromise();
    const deletableItemsIds = this.filesRepositoryService.getDeletableItemsIds(
      selectedItemsIds,
      dataGridDataSourceData
    );
    let body: string;
    if (deletableItemsIds.length < selectedItemsIds.length) {
      body = gettext(`
        You are about to delete the selected files.
        Note: the selected files of type "c8y_applications_storage_*" won't be deleted though
        - such files can be deleted only from the "Activity log" of the associated application.
        Do you want to proceed?
      `);
    } else {
      body = gettext('You are about to delete the selected files. Do you want to proceed?');
    }

    return this.deleteItemsWithConfirmation(deletableItemsIds, {
      title: gettext('Delete files'),
      body,
      successText: gettext('Files deleted.')
    });
  }

  async onDownloadItem(selectedItem: IManagedObjectBinary) {
    return this.fileService.download(selectedItem);
  }

  openFileUploadComponent() {
    const modalOptions: ModalOptions<FilesRepositoryUploadComponent> = {
      class: 'modal-sm',
      ariaDescribedby: 'modal-body',
      ariaLabelledBy: 'modal-title',
      ignoreBackdropClick: true
    };
    const modalRef = this.bsModalService.show(FilesRepositoryUploadComponent, modalOptions);
    modalRef.content.onClose.pipe(takeUntil(this.destroy$)).subscribe(({ uploaded }) => {
      if (uploaded) {
        this.refresh.emit();
      }
      modalRef.hide();
    });
  }

  private async deleteItemsWithConfirmation(
    selectedItemsIds: string[],
    options: { title: string; body: string; successText: string }
  ) {
    try {
      await this.confirmItemsDeletion(options);
      this.isLoading = true;
      const promises = selectedItemsIds.map(selectedItemId =>
        this.inventoryBinaryService.delete(selectedItemId)
      );
      await Promise.all(promises);
      this.alertService.success(options.successText);
      this.refresh.next();
    } catch (ex) {
      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    } finally {
      this.isLoading = false;
    }
  }

  private async confirmItemsDeletion({ title, body }: { title: string; body: string }) {
    const status = Status.DANGER;
    const labels = { ok: gettext('Delete'), cancel: gettext('Cancel') };
    await this.modalService.confirm(title, body, status, labels);
  }
}
